@import url(https://fonts.googleapis.com/css2?family=Comforter+Brush&family=Yellowtail&display=swap);
html{
  box-sizing: border-box;
}
body {
  overflow-y: hidden;
  margin: 0;
  padding: 0;
  font-family: Comforter Brush,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div#root{
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}
p{
  font-family: Yellowtail,sans-serif;
}

.card-container{
    margin: 10px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    background: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    height: 290px;
    width: 200px;
    box-shadow: 0 10px 20px 5px rgba(0, 0, 0,0.5)
}
img{
    height: 200px;
    width: 200px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
h2{
    text-align: center;
    margin: 0;
    padding: 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
p{
    text-align: center;
    margin: 0;
    padding: 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.card-container:active{
    transform: translateY(2px);
    box-shadow: 0 5px 10px 1px rgba(0, 0, 0,0.5)
}

.card-area{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 95%;
    margin: 0 2.5%;
}
.navigation-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5px;
}

h1{
    margin: 0;
    font-size: 100px;
    text-align: center;
}

input{
    text-align: center;
    outline: none;
    height: 50px;
    width: 300px;
    border-radius: 20px;
}


@media screen and (max-width:600px){
    h1{
        font-size: 70px;
    }
}
.scroll{
    height: 60vh;
    overflow-y: auto;
    margin: 15px 0;
}


@media screen and (max-width:600px) {
    .scroll{
        height: 68vh;
    }
}
